import { STORAGE_KEY, StorageLogin } from './constance';

export const getStorage = (): StorageLogin | null => {
  const data = window.localStorage.getItem(STORAGE_KEY) as string;
  try {
    return data && JSON.parse(data) || {};
  } catch (error) {
    return null;
  }
};

export const setPhone = (phone: string) => {
  window.localStorage.setItem(STORAGE_KEY, JSON.stringify({ phone }));
};
